.portfolio__container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  justify-items: center;
}

.portfolio__item {
  margin: 1rem;
  max-width: 300px;
}

article a {
}

@media screen and (max-width: 1200px) {
  .portfolio__container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media screen and (max-width: 900px) {
  .portfolio__container {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
  }
}
