  font-size: 3rem;
  left: 40px;
}

header {
  background: linear-gradient(
    -45deg,
    rgba(252, 252, 252, 0.427) 0%,
    rgba(0, 213, 255, 0.632) 15%,
    rgba(0, 255, 4, 0.461) 90%,
    rgb(9, 155, 14) 15%
  );
  /* border: brown solid 1px; */
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px,
    rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
  border-radius: 1rem;
  margin-bottom: 1rem;
  min-height: 100px;
}

header:hover {
  background: linear-gradient(
    -45deg,
    rgba(0, 255, 4, 0.461) 90%,
    rgba(252, 252, 252, 0.427) 0%,
    rgba(0, 213, 255, 0.632) 15%,
    rgb(9, 155, 14) 15%
  );
  transition: var(--transition);
}
